.progress-bar {
  height: 10px;
  background-color: rgb(255, 195, 173);
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  width: 91%;
}

.progress-fill {
  height: 100%;
  background-color: rgb(255, 127, 80);
  transition: width 0.5s ease-in-out;
}

.road-track {
  display: flex;
  gap: 4px;
  margin: 20px 0;
}

.road-segment {
  flex: 1;
  height: 10px;
  background-color: #e0e0e0;
  border-radius: 5px;
  position: relative;
}

.road-segment.filled {
  background-color: #ffd700;
}

.buttonGo {
  margin-bottom: 10%;
}

.road-icon {
  position: absolute;
  right: -10px;
  top: -15px;
  font-size: 24px;
}

.circular-progress {
  position: relative;
  width: 100px;
  height: 100px;
}

.progress-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.perents-of-home {
